import 'ol/ol.css';
import GPX from 'ol/format/GPX';
import {Map, View} from 'ol';
import TileLayer from 'ol/layer/Tile';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import OSM from 'ol/source/OSM';
import {Circle, Fill, Stroke, Style} from 'ol/style';
import { transform } from 'ol/proj';

// from https://openlayers.org/en/latest/doc/tutorials/bundle.html
// & https://openlayers.org/en/latest/examples/gpx.html

const style = {
    'Point': new Style({
      image: new Circle({
        fill: new Fill({
          color: 'rgba(255,255,0,0.5)'
        }),
        radius: 5,
        stroke: new Stroke({
          color: '#ff0',
          width: 1
        })
      })
    }),
    'LineString': new Style({
      stroke: new Stroke({
        color: '#f00',
        width: 3
      })
    }),
    'Polygon': new Style({
      fill: new Fill({
        color: 'rgba(0,255,255,0.5)'
      }),
      stroke: new Stroke({
        color: '#0ff',
        width: 1
      })
    }),
    'MultiPoint': new Style({
      image: new Circle({
        fill: new Fill({
          color: 'rgba(255,0,255,0.5)'
        }),
        radius: 5,
        stroke: new Stroke({
          color: '#f0f',
          width: 1
        })
      })
    }),
    'MultiLineString': new Style({
      stroke: new Stroke({
        color: '#00f',
        width: 3
      })
    }),
    'MultiPolygon': new Style({
      fill: new Fill({
        color: 'rgba(25,120,255,0.5)'
      }),
      stroke: new Stroke({
        color: '#00f',
        width: 1
      })
    })
};
const map = new Map({
  target: 'map',
  layers: [
    new TileLayer({
      source: new OSM()
    })
  ],
  view: new View({
    center: [0, 0],
    zoom: 0
  })
});
const lgpx = new VectorLayer({
  source: new VectorSource({
    url: 'https://openlayers.org/en/v4.6.5/examples/data/gpx/fells_loop.gpx',
    format: new GPX(),
  }),
  style: function (feature) {
    return style[feature.getGeometry().getType()];
  },
});
map.addLayer(lgpx);

const fs = require('fs');
let str = "";

try {
  str = fs.readFileSync('data1.gpx', 'utf8'); // openrouteservice
} catch (e) {
  console.log(e);
}
const v = new VectorLayer({
  source: new VectorSource(),
  style: function (feature) {
    return style[feature.getGeometry().getType()];
  },
});
map.addLayer(v);

const GPXfeatures = (new GPX()).readFeatures(str, {featureProjection: 'EPSG:3857'});
v.getSource().addFeatures(GPXfeatures);

map.on('click', (e)=>{
  console.log(transform(e.coordinate, 'EPSG:3857', 'EPSG:4326'));
});

// using "EPSG:3857", as default
